.page {
  font-family: "IBM Plex Sans";
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;

  scroll-behavior: smooth;

  &__body {
    box-sizing: border-box;

    // &--with-menu {
    //   overflow: hidden;
    // }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 88px;

    @include onDesktop {
      margin-bottom: 200px;
    }

    &--header {
      margin-bottom: 104px;
    }

    &--events {
      margin-bottom: 104px;
    }

    &--lection {
      margin-bottom: 380px;
    }
  }
}
